* {
  margin: 0;
  padding: 0;
  position: relative;

  text-decoration: none;
}
h1 {
  font-size: 25px;
  padding-top: 15px;
}
.link {
  display: block;
  height: 240px;
}
.header {
  background: black;
  padding: 20px;
  z-index: 5;
  position: sticky;
  top: 0.1px;
}

.body {
  display: flex;
  height: 100%;
  max-width: 1820px;
  flex-direction: row-reverse;
  justify-content: space-between;
}

/* Menu */
.menu {
  position: relative;
  background: rgb(152, 239, 152);
  display: flex;
  border-radius: 5px;
  align-items: center;
  padding: 5px 20px;
  flex-direction: row;
  justify-content: space-between;
}

.menu__logo_image {
  height: 40px;
  width: 40px;
  padding-left: 2px;
}

.menu__logo_text {
  color: black;
  padding-top: 1px;
  font-size: 35px;
  font-weight: bold;
  font-family: "Times New Roman", Times, serif;
}

.menu__logo {
  padding-left: 5px;
  display: flex;
  cursor: pointer;
}

.menu__search_block {
  display: flex;
  flex-direction: column;
}

.menu__search {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 110px;
  width: 600px;
  height: 50px;
}

.menu__search_input {
  width: 550px;
  height: 40px;
  font-size: 20px;
  padding-right: 50px;
}

.menu__search_logo {
  position: absolute;
  cursor: pointer;
  height: 30px;
  margin-left: 550px;
}

.menu__search_display {
  padding: 0px 2px;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  font-weight: bold;
  text-decoration: none;
  margin-top: 50px;
  width: 600px;
  max-height: 600px;
  margin-left: 110px;
  background: #fff;
  font-size: 17px;
  z-index: 1;
}

.menu__search_display_invise {
  display: none;
}

.menu__search_display_item {
  color: black;

  margin-top: 5px;
  cursor: pointer;
  height: 30px;
  display: flex;
  align-items: center;
  border: 1px solid black;
  background: rgb(214, 213, 213);
}

.menu__basket {
  padding-right: 10px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  cursor: pointer;
}

