/* Catalog */
.catalog {
  transition: width 0.5s;
  justify-content: space-around;
  height: 100%;
}

.catalog__head {
  border-bottom: 1px solid black;

  display: flex;
  padding: 15px 0px;

  justify-content: center;
}

.catalog__head_category {
  background: rgb(255, 255, 255);
  padding: 10px;
  text-align: center;
  font-size: 25px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  border-radius: 20px;
}

.catalog__body {
  justify-content: center;

  display: grid;
  grid-template-columns: repeat(4, 287px);
  flex-wrap: wrap;
  height: 100%;
  position: relative;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-left: 40px;
}

