/* Side nav*/

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  margin-top: 100px;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 20px solid black;
  background: white;
}

.categories__sideNav_open_bkg {
  position: fixed;
  top: 120px;
  margin-left: 5px;
  left: 0;
  display: flex;
  width: 30px;
  height: 30px;
  background: white;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  border-radius: 100%;
  z-index: 3;
  transition: margin-left 0.5s;
}

.categories__sideNav_open {
  position: fixed;
  display: flex;
  cursor: pointer;
  height: 50px;
  width: 50px;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  color: rgb(136, 191, 228);
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
  cursor: pointer;
}

#main {
  transition: margin-left 0.5s;
  padding: 20px;
}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }
  .sidenav a {
    font-size: 18px;
  }
}
